
import { GlobalTimingService, CountDownTimeoutMode } from 'awp-plugin-timing';

// let globalTimer = new CountDownTimer();

module.exports.install = (Vue, options) => {
  console.log('Vue GlobalTimer install');

  Vue.prototype.$globalTimer = GlobalTimingService.getCountDownTimer();
  Vue.prototype.$globalTimerMode = CountDownTimeoutMode;
};
